.nav{
    display: flex;
    color: white;
    background-image: linear-gradient(to right, #051421,#042727); 
    overflow-x: scroll;
    padding:10px;
    padding-left: 40px;
}
.nav::-webkit-scrollbar{
    display: none;
}
.nav{
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.nav > h2{
    cursor: pointer;
    margin-right:30px;
    margin-left:30px;
    font-weight: 500;
    opacity: 0.7;
    transition: transform 50ms ease-out;
}
.nav >h2:hover{
    opacity: 1;
    transform: scale(1.2);
}
